* {
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  width: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

input,
textarea,
select{
  outline: none;
}

h2,
h3 {
  font-weight: 700;
  color: #231F1F;
  margin: 0;
}

.second-title {
  line-height: 24px;
  font-size: 24px;
  margin-bottom: 24px;
}

.third-title {

}

ul,
li {
  display: block;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

header,
section,
footer {
  position: relative;
}

.container {
  width: 1200px;
}