.payment {
  display: flex;

  &-limits {
    color: #5B78B9;
  }

  &__left {
    width: 35%;
    transition: .2s ease;
  }

  &__right {
    width: 65%;
  }

  &__info {
    padding: 36px 23px 42px;
    background: linear-gradient(180deg, #2931EB 0%, #151CC3 100%);
    color: #ffffff;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;

    .second-title {
      color: #ffffff;
    }
  }

  &__method {
    padding: 36px;
  }

  &__form {
    padding: 36px;
  }
}

.info {
  &__block {
    &_buying {
      font-size: 18px;
    }

    &_cost {
      margin-top: 28px;
      margin-bottom: 42px;
    }

    &_seller {
      font-size: 17px;
    }
  }

  &__cost {
    line-height: 52px;
    font-size: 34px;
    text-transform: uppercase;

    &_text {
      line-height: normal;
      font-size: 16px;
      color: #B4B7F8;
    }
  }

  &__text {
    line-height: 27px;
    font-size: 18px;
    color: #FFFFFF;
    font-weight: 600;

    &_nasa:after {
      content: '';
      display: inline-block;
      background: url("../img/planet.png") no-repeat center / cover;
      width: 23px;
      height: 17px;
    }

    &_small {
      font-size: 16px;
    }

    &_dark {
      color: #B4B7F8;
    }
  }


  &__provide {
    margin-top: auto;

    &_text {
      font-size: 18px;
    }
  }
}

.method {

  &__list-select {
    display: flex;
    justify-content: space-around;
  }

  &__list-item {
    width: 100%;
    text-align: center;
    overflow: hidden;

    &:first-child {
      border-top-left-radius: 5px;
      border-bottom-left-radius: 5px;
    }

    &:last-child {
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
    }
  }

  &__btn {
    color: #5B78B9;
    display: inline-block;
    background: #EDF2FD;
    padding: 7px 0 7px 0;
    width: 100%;
    transition: .2s ease;

    &_active,
    &:hover {
      background: $accent;
      color: #ffffff;
    }
  }

  &__e-money {
    margin-top: 14px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 24px;
    text-align: center;
    color: #231F1F;
  }
}

.wallet {

  &__logo {
    height: 100px;
    background: #EDF2FD;
    border-radius: 6px;
    background-position: center;
    background-repeat: no-repeat;
    transition: .2s ease;
    cursor: pointer;
  }

  &-active {
    .wallet__logo {
      background-color: #fff;
      border: 3px solid #2931EB;
    }
  }

  &__name {
    font-weight: 700;
    margin-top: 10px;
  }
}

.form {
  &__row {
    display: flex;
    align-items: center;
    padding: 12px 0;
    position: relative;

    &_textarea {
      align-items: normal;
    }
  }

  &__label {
    width: 204px;
    margin-right: 18px;
  }

  &__input,
  &__textarea {
    padding: 12px 16px;
    width: 308px;
    border-radius: 6px;
    border: 2px solid #B3B3B3;
    transition: .2s ease;

    &::placeholder {
      font-family: "Open Sans", sans-serif;
      font-size: 16px;
      color: $gray;
    }

    &:focus {
      border: 2px solid #2931EB;
    }
  }

  &__sbm {
    background: $accent;
    color: #ffffff;
    padding: 12px 36px;
    border: none;
    border-radius: 6px;
    font-size: 18px;
    margin: 31px auto 0;
    display: block;
    transition: .2s ease;
    cursor: pointer;

    &:hover {
      background: darken($accent, 10%);
    }

  }

  &__usd {
    position: absolute;
    color: $gray;
    right: 194px;
    font-weight: 600;
  }

  &__text {
    font-size: 15px;
    line-height: 23px;
    padding-left: 222px;

    .form__link {
      color: $accent;
      text-decoration: underline #BFC2F9;
    }
  }

  &__input_money {
    border: 2px solid #EFEFEF;
    background: #EFEFEF;
  }

  &__input_captcha {
    width: 218px;
    margin-right: 8px;
  }

  &__textarea {
    flex-grow: 1;
    resize: none;
    height: 140px;
  }

  &__fees {
    width: 170px;
    margin-left: 10px;
    color: $gray;
  }
}

.captcha-box {
  display: flex;
}

.captcha-img {
  width: 82px;
  background: #EFEFEF;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.second-title_wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@each $i in qiwi, skrill, payoneer, payza, yandex, paypal {
  .#{$i} > .wallet__logo {
    background-image: url("../img/#{$i}.png");
  }
}

.method__e-money-select {
  padding: 12px 16px;
  width: 100%;
  border-radius: 6px;
  border: 2px solid #B3B3B3;
  transition: .2s ease;
  display: none;

  &::placeholder {
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
    color: $gray;
  }

  &:focus {
    border: 2px solid #2931EB;
  }
}

.open-pay {
  background: $accent;
  width: 100%;
  height: 60px;
  position: absolute;
  left: 0;
  top: 0;
  justify-content: space-between;
  padding: 0 16px;
  color: #ffffff;
  border: none;
  font-size: 16px;
  font-weight: 600;
  display: none;
  z-index: 11;

  &:after {
    content: '';
    position: absolute;
    width: 28px;
    height: 2px;
    background: #6066F3;
    border-radius: 5px;
    margin-left: -10px;
    left: 50%;
    bottom: 8px;
  }
}

.open{
  top: 58px !important;
}