@media screen and (max-width: 1199px) {
  .container {
    width: 960px;
  }

  .form__fees {
    width: auto;
    position: absolute;
    right: 27px;
    top: -12px;
    margin: 0;
  }

  .form__usd {
    right: 36px;
  }

  .form__textarea {
    flex-grow: 0;
  }

  .method__btn {
    font-size: 13px;
  }
}

@media screen and (max-width: 991px) {
  .container {
    width: 720px;
  }

  .payment__left {
    width: 100%;
    position: absolute;
    max-height: 0;
    overflow: hidden;
    z-index: 10;
    padding: 10px;
    top: 0;
    left: 0;
  }

  .info__block_cost {
    margin-top: 15px;
    margin-bottom: 0;
  }

  .payment__info{
    padding: 17px;
    border-radius: 6px;
  }

  .info__block_seller{
    display: none;
  }

  .info__provide{
    display: none;
  }

  .open-pay{
    display: flex;
  }

  .payment__method {
    padding-top: 80px;
  }

  .payment__right {
    width: 100%;
  }

  .form__fees {
    right: 121px;
  }

  .form__usd {
    right: 132px;
  }

  .form__textarea {
    flex-grow: 1;
  }
}

@media screen and (max-width: 767px) {
  .container {
    width: 540px;
  }

  .method__e-money {
    grid-template-columns: repeat(3,1fr);
  }

  .method__list-select{
    display: none;
  }

  .payment-limits{
    display: none;
  }

  .payment__method {
    padding-top: 80px;
  }

  .form__row{
    display: block;
  }

  .form__input, .form__textarea{
    width: 100%;
  }

  .form__usd {
    right: 19px;
    top: 42px;
  }

  .form-dn{
    display: none;
  }

  .form__fees {
    right: 8px;
    top: 9px;
  }

  .form__input_captcha{
    width: 80%;
  }

  .captcha-img{
    width: 20%;
  }

  .form__text{
    padding: 0;
  }
}

@media screen and (max-width: 575px) {
  .container {
    width: auto;
  }

  .method__e-money{
    display: none;
  }

  .method__e-money-select{
    display: block;
  }

  .payment__form, .payment__method {
    padding: 16px;
  }

  .payment__method {
    padding-top: 80px;
  }

  .form__input_captcha {
    width: 72%;
  }

  .captcha-img {
    width: 28%;
  }

  .form__fees {
    right: 5px;
    top: 14px;
    font-size: 14px;
  }

  .form__textarea{
    height: 84px;
  }
}